ul {
  list-style: none;
}

.container .alert {
  font-size: 1.8rem;
  text-align: center;
}

.container .alert a {
  margin-right: 15px;
  cursor: pointer;
}

.container .alert a:hover {
  text-decoration: underline;
}

.notes-container,
ul {
  display: flex;
  flex-direction: row;
  flex-wrap: no-wrap;
  justify-content: flex-start;
  height: 300px;
}

.notes-container {
  background: url("./assets/noteslines.png") repeat-x center left;
}

.notes-container .trebleclef {
  background: url("./assets/trebleclef.png") no-repeat left top;
  width: 125px;
}

.notes-container .note-type-44 {
  background: url("./assets/44.png") no-repeat left top;
  width: 65px;
}

.notes-container .note-type-34 {
  background: url("./assets/34.png") no-repeat left top;
  width: 65px;
}

.notes-container ul {
  background: url("./assets/bardivider.png") no-repeat right top;
}

.notes-container ul li {
  width: 105px;
}
.notes-container ul li.minim {
  width: 210px;
}
.notes-container ul li.semibreve {
  width: 420px;
}

/*
  128px c
  110px d
  88px e
  71px f
  52px g
  35px a
  48px b transform: rotate(180deg);
*/

li.chrochet {
  background-image: url("./assets/crochet.png");
  background-repeat: no-repeat;
}
li.chrochet.c {
  background: url("./assets/cbar.png") no-repeat 0px 249px,
    url("./assets/crochet.png") no-repeat 10px 128px;
  margin-right: 5px;
}

li.minim {
  background-image: url("./assets/minim.png");
  background-repeat: no-repeat;
}
li.minim.c {
  background: url("./assets/cbar.png") no-repeat 0px 241px,
    url("./assets/minim.png") no-repeat 10px 132px;
  margin-right: 5px;
}

li.semibreve {
  background-image: url("./assets/semibreve.png");
  background-repeat: no-repeat;
}
li.semibreve.c {
  background: url("./assets/cbar.png") no-repeat 0px 241px,
    url("./assets/semibreve.png") no-repeat 10px 132px;
  margin-right: 5px;
}

li.semibreve.d {
  background-position: left 122px;
}
li.d {
  background-position: left 110px;
}

li.semibreve.e {
  background-position: left 100px;
}
li.e {
  background-position: left 88px;
}

li.semibreve.f {
  background-position: left 83px;
}
li.f {
  background-position: left 71px;
}

li.semibreve.g {
  background-position: left 64px;
}
li.g {
  background-position: left 52px;
}

li.semibreve.a {
  background-position: left 47px;
}
li.a {
  background-position: left 35px;
}

li.b,
li.rc {
  transform: rotate(180deg);
}
li.minim.b {
  background-position: 150px 34px;
}
li.semibreve.b {
  background-position: 0 30px;
  transform: rotate(0deg);
}
li.chrochet.b {
  background-position: 55px 35px;
}

li.rc {
  background-position: 55px 52px;
}
li.semibreve.rc {
  background-position: 366px 65px;
}
